<template>
  <el-container>
    <el-header class="cate_mana_header">
      <el-input
        placeholder=""
        v-model="roleName" style="width: 200px;">
      </el-input>
      <el-button type="primary" size="medium" style="margin-left: 10px" @click="addNewRole"></el-button>
    </el-header>
    <el-main class="cate_mana_main">
      <el-table
        ref="multipleTable"
        :data="roles"
        tooltip-effect="dark"
        style="width: 100%">
<!--        @selection-change="handleSelectionChange" v-loading="loading">-->
        <el-table-column
          width="55" align="left">
        </el-table-column>
        <el-table-column
          label="Id"
          prop="id"
          width="120" align="left">
        </el-table-column>
        <el-table-column
          label=""
          prop="roleName"
          width="120" align="left">
        </el-table-column>
        <el-table-column label="" align="left">
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="handleEdit(scope.$index, scope.row)">
            </el-button>
<!--            <el-button-->
<!--              size="mini"-->
<!--              type="danger"-->
<!--              @click="handleDelete(scope.$index, scope.row)">-->
<!--            </el-button>-->
          </template>
        </el-table-column>
      </el-table>
<!--      <el-button type="danger" :disabled="this.selItems.length==0" style="margin-top: 10px;width: 100px;"-->
<!--                 @click="deleteAll" v-if="this.roles.length>0">-->
<!--      </el-button>-->
    </el-main>
  </el-container>
</template>
<script>
  import {postRequest} from '../utils/api'
  import {putRequest} from '../utils/api'
  import {deleteRequest} from '../utils/api'
  import {getRequest} from '../utils/api'
  export default{
    methods: {
      addNewRole(){
        this.loading = true;
        var _this = this;
        postRequest('/admin/role/', {roleName: this.roleName}).then(resp=> {
          if (resp.status == 200) {
            var json = resp.data;
            _this.$message({type: json.status, message: json.msg});
            _this.roleName = '';
            _this.refresh();
          }
          _this.loading = false;
        }, resp=> {
          if (resp.response.status == 403) {
            _this.$message({
              type: 'error',
              message: resp.response.data
            });
          }
          _this.loading = false;
        });
      },
      // deleteAll(){
      //   var _this = this;
      //   this.$confirm(' ' + this.selItems.length + ' ?', '', {
      //     type: 'warning',
      //     confirmButtonText: '',
      //     cancelButtonText: ''
      //   }).then(()=> {
      //     var selItems = _this.selItems;
      //     var ids = '';
      //     for (var i = 0; i < selItems.length; i++) {
      //       ids += selItems[i].id + ",";
      //     }
      //     _this.deleteCate(ids.substring(0, ids.length - 1));
      //   }).catch(() => {
      //     //
      //     _this.loading = false;
      //   });
      // },
      handleSelectionChange(val) {
        this.selItems = val;
      },
      handleEdit(index, row){
        var _this = this;
        this.$prompt('', '', {
          confirmButtonText: '',
          inputValue: row.roleName,
          cancelButtonText: ''
        }).then(({value}) => {
          //value
          if (value == null || value.length == 0) {
            _this.$message({
              type: 'info',
              message: '!'
            });
          } else {
            _this.loading = true;
            putRequest("/admin/role/", {id: row.id, roleName: value}).then(resp=> {
              var json = resp.data;
              _this.$message({
                type: json.status,
                message: json.msg
              });
              _this.refresh();
            }, resp=> {
              if (resp.response.status == 403) {
                _this.$message({
                  type: 'error',
                  message: resp.response.data
                });
              }
              _this.loading = false;
            });
          }
        });
      },
      // handleDelete(index, row){
      //   let _this = this;
      //   this.$confirm(' ' + row.roleName + ' ?', '', {
      //     confirmButtonText: '',
      //     cancelButtonText: '',
      //     type: 'warning'
      //   }).then(() => {
      //     _this.deleteCate(row.id);
      //   }).catch(() => {
      //     //
      //     _this.loading = false;
      //   });
      // },
      // deleteCate(ids){
      //   var _this = this;
      //   this.loading = true;
      //   //
      //   deleteRequest("/admin/role/" + ids).then(resp=> {
      //     var json = resp.data;
      //     _this.$message({
      //       type: json.status,
      //       message: json.msg
      //     });
      //     _this.refresh();
      //   }, resp=> {
      //     _this.loading = false;
      //     if (resp.response.status == 403) {
      //       _this.$message({
      //         type: 'error',
      //         message: resp.response.data
      //       });
      //     } else if (resp.response.status == 500) {
      //       _this.$message({
      //         type: 'error',
      //         message: '!'
      //       });
      //     }
      //   })
      // },
      refresh(){
        let _this = this;
        getRequest("/admin/role/all").then(resp=> {
          _this.roles = resp.data;
          _this.loading = false;
        }, resp=> {
          if (resp.response.status == 403) {
            _this.$message({
              type: 'error',
              message: resp.response.data
            });
          }
          _this.loading = false;
        });
      }
    },
    mounted: function () {
      this.loading = true;
      this.refresh();
    },
    data(){
      return {
        roleName: '',
        selItems: [],
        roles: [],
        loading: false
      }
    }
  }
</script>
<style>
  .cate_mana_header {
    background-color: #ececec;
    margin-top: 20px;
    padding-left: 5px;
    display: flex;
    justify-content: flex-start;
  }

  .cate_mana_main {
    /*justify-content: flex-start;*/
    display: flex;
    flex-direction: column;
    padding-left: 5px;
    background-color: #ececec;
    margin-top: 20px;
    padding-top: 10px;
  }
</style>



